@charset "utf-8";
/* ***************  global *************** */
// Montserrat 400, 600
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600');

* {box-sizing: border-box}
html, body {margin:0; padding:0; font-family: 'NotoSansCJKkr', serif; color: #333;}

.clearfix:after {content:"."; display:block; height:0; clear:both; visibility:hidden; } .clearfix { display:inline-block; }/*  ie-mac \*/ *html .clearfix {height: 1%;} .clearfix {display: block;}/* ie-mac */

/* *********** other ********** */
summary { display:none}
/* link */
a {}
a:hover {}
a:link {text-decoration:none}
a:visited {text-decoration:none}
a:active {text-decoration:none}

/* image */
img {border:0; vertical-align:top}

/* define */
dl, dt, dd {margin:0px; padding:0px;}

/* sentence */
div, h2 {padding:0; margin:0;}
p {margin:0px; padding:0px;}

/* list */
ul {list-style-type:none; margin:0px; padding:0px; }
ol {padding-left:24px;}

/* area */
hr {display:none;}

/* form */
form {margin:0; padding:0;}
form label, label {height:19px; color:#5E5E5E; cursor:default; font-size:100%;}
select {font-size:100%; background:#fff; vertical-align:middle; height:20px; letter-spacing:0px;}
legend {display:none;}
fieldset {margin:0; padding:0; border:none;}

/* table */
table {margin:0px; padding:0px; border-spacing:0;}
td {margin:0px; padding:0px; font-size:100%; color:#777777;}
.notable {border-collapse:collapse; margin:0; padding:0; border-spacing:0; border:0 solid;}
th {font-weight:normal;}
/* h */
h1, h2, h3, h4, h5, h6 { margin:0; padding:0;  font-weight:normal;}

input[type="number"]::-webkit-outer-spin-button,input[type="number"]::-webkit-inner-spin-button {-webkit-appearance: none; margin: 0;}

/* border */
.nobd {border:0 !important}

@font-face {
    font-family: 'NotoSansCJKkr';
    src: url('../img/font/notokr-regular.eot');
    src: url('../img/font/notokr-regular.eot?#iefix') format('embedded-opentype'),
         url('../img/font/notokr-regular.woff2') format('woff2'),
         url('../img/font/notokr-regular.woff') format('woff'),
         url('../img/font/notokr-regular.ttf') format('truetype'),
         url('../img/font/notokr-regular.svg#notokr-regular') format('svg');
}

